/**
 * Theme Manager - Handles theme, font, and sound preferences
 */

import { getItem, setItem, getBool, setBool } from '../utils/storage.js';

export class ThemeManager {
  constructor() {
    this.theme = getItem('theme', 'dark');
    this.dyslexicFont = getBool('dyslexicFont', false);
    this.soundEnabled = getItem('soundEnabled') !== 'false';
  }

  init() {
    this.applyPersistedSettings();
    this.setupEventListeners();
  }

  applyPersistedSettings() {
    if (this.theme === 'light') {
      document.body.classList.add('light-theme');
    }
    if (this.dyslexicFont) {
      document.body.classList.add('dyslexic-font');
    }
    if (!this.soundEnabled) {
      const soundToggle = document.getElementById('soundToggle');
      if (soundToggle) soundToggle.classList.add('muted');
    }
  }

  setupEventListeners() {
    const themeSwitcher = document.getElementById('themeSwitcher');
    const fontToggle = document.getElementById('fontToggle');
    const soundToggle = document.getElementById('soundToggle');

    if (themeSwitcher) {
      themeSwitcher.addEventListener('click', () => this.toggleTheme());
    }
    if (fontToggle) {
      fontToggle.addEventListener('click', () => this.toggleDyslexicFont());
    }
    if (soundToggle) {
      soundToggle.addEventListener('click', () => this.toggleSound());
    }
  }

  toggleTheme() {
    this.theme = this.theme === 'dark' ? 'light' : 'dark';
    document.body.classList.toggle('light-theme');
    const icon = document.querySelector('#themeSwitcher i');
    if (icon) {
      icon.className = this.theme === 'dark' ? 'fas fa-moon' : 'fas fa-sun';
    }
    setItem('theme', this.theme);
  }

  toggleDyslexicFont() {
    this.dyslexicFont = !this.dyslexicFont;
    document.body.classList.toggle('dyslexic-font');
    setBool('dyslexicFont', this.dyslexicFont);
    this.playSound('click');
  }

  toggleSound() {
    this.soundEnabled = !this.soundEnabled;
    const toggle = document.getElementById('soundToggle');
    if (!toggle) return;

    const icon = toggle.querySelector('i');
    if (this.soundEnabled) {
      toggle.classList.remove('muted');
      if (icon) icon.className = 'fas fa-volume-up';
    } else {
      toggle.classList.add('muted');
      if (icon) icon.className = 'fas fa-volume-mute';
    }
    setItem('soundEnabled', this.soundEnabled);
  }

  playSound(type) {
    if (!this.soundEnabled) return;
    // Sound playback would be implemented here
    console.log(`Playing sound: ${type}`);
  }

  isSoundEnabled() {
    return this.soundEnabled;
  }
}
