/**
 * Gear Comparison - Gear modal with comparison display
 */

import { gearData, defaultGear } from '../data/gearData.js';
import { highlightElement } from '../utils/dom.js';

export class GearComparison {
  constructor(playSound = () => {}) {
    this.playSound = playSound;
    this.modal = null;
    this.modalTitle = null;
    this.modalContent = null;
  }

  init() {
    this.modal = document.getElementById('gearModal');
    this.modalTitle = document.getElementById('modalTitle');
    this.modalContent = document.getElementById('modalContent');

    this.setupGearSlots();
    this.setupCloseButton();
  }

  setupGearSlots() {
    document.querySelectorAll('.gear-slot').forEach(slot => {
      slot.addEventListener('click', () => {
        this.showComparison(slot);
      });
    });
  }

  setupCloseButton() {
    const closeBtn = document.getElementById('closeModal');
    if (closeBtn) {
      closeBtn.addEventListener('click', () => this.hide());
    }
  }

  showComparison(slot) {
    const gearType = slot.dataset.gear;
    const data = gearData[gearType] || defaultGear;

    this.modalTitle.textContent = `${gearType.charAt(0).toUpperCase() + gearType.slice(1)} Comparison`;

    this.renderContent(data);

    this.modal.style.display = 'block';
    this.playSound('click');
    highlightElement(slot);
  }

  renderContent(data) {
    // Clear existing content
    while (this.modalContent.firstChild) {
      this.modalContent.removeChild(this.modalContent.firstChild);
    }

    // Create comparison grid
    const grid = document.createElement('div');
    grid.className = 'modal-comparison-grid';

    // Primary card
    grid.appendChild(this.createComparisonCard(
      'primary',
      'Primary Choice',
      'fa-star',
      'var(--gold)',
      data.primary,
      data.primaryStats
    ));

    // Alternative card
    grid.appendChild(this.createComparisonCard(
      'alternative',
      'Alternative',
      'fa-exchange-alt',
      'var(--orange)',
      data.alternative,
      data.alternativeStats
    ));

    this.modalContent.appendChild(grid);

    // Tempering box
    const temperBox = document.createElement('div');
    temperBox.className = 'tempering-box';

    const strong = document.createElement('strong');
    const hammerIcon = document.createElement('i');
    hammerIcon.className = 'fas fa-hammer';
    strong.appendChild(hammerIcon);
    strong.appendChild(document.createTextNode(' Recommended Tempering: '));

    temperBox.appendChild(strong);
    temperBox.appendChild(document.createTextNode(data.tempering));

    this.modalContent.appendChild(temperBox);
  }

  createComparisonCard(type, title, iconClass, color, itemName, stats) {
    const card = document.createElement('div');
    card.className = `comparison-card ${type}`;

    // Header
    const header = document.createElement('h4');
    header.style.color = color;
    header.style.marginBottom = '10px';

    const icon = document.createElement('i');
    icon.className = `fas ${iconClass}`;
    header.appendChild(icon);
    header.appendChild(document.createTextNode(` ${title}`));
    card.appendChild(header);

    // Item name
    const nameDiv = document.createElement('div');
    nameDiv.className = 'item-name';
    nameDiv.textContent = itemName;
    card.appendChild(nameDiv);

    // Stats list
    const ul = document.createElement('ul');
    stats.forEach(stat => {
      const li = document.createElement('li');
      li.textContent = stat;
      ul.appendChild(li);
    });
    card.appendChild(ul);

    return card;
  }

  hide() {
    if (this.modal) {
      this.modal.style.display = 'none';
    }
  }
}
