/**
 * Damage Calculator - D4 damage calculation logic
 */

export class DamageCalculator {
  constructor() {
    this.inputs = [
      'weaponDamage',
      'intelligence',
      'bloodWaveRanks',
      'tidalWaves',
      'additiveDamage',
      'multiplicativeDamage',
      'shadowDamage',
      'critChance',
      'critDamage',
      'vulnerableDamage',
      'overpowerDamage',
      'overpowerChance',
      'doubleDamageChance'
    ];
  }

  init() {
    this.setupInputListeners();
    this.calculate();
  }

  setupInputListeners() {
    this.inputs.forEach(inputId => {
      const el = document.getElementById(inputId);
      if (el) {
        el.addEventListener('input', () => this.calculate());
      }
    });
  }

  getValue(id) {
    const el = document.getElementById(id);
    return parseInt(el?.value) || 0;
  }

  calculate() {
    const weaponDps = this.getValue('weaponDamage');
    const int = this.getValue('intelligence');
    const critChance = this.getValue('critChance');
    const critDamage = this.getValue('critDamage');
    const overpowerDamage = this.getValue('overpowerDamage');
    const overpowerChance = this.getValue('overpowerChance');
    const shadowDamage = this.getValue('shadowDamage');
    const vulnerableDamage = this.getValue('vulnerableDamage');
    const additiveDamage = this.getValue('additiveDamage');
    const multiDamage = this.getValue('multiplicativeDamage');
    const doubleChance = this.getValue('doubleDamageChance');
    const ranks = this.getValue('bloodWaveRanks') || 1;
    const tidalWaves = this.getValue('tidalWaves') || 1;

    // D4-accurate formula logic
    const baseDamage = weaponDps * (1 + int / 1000);
    const additiveMultiplier = 1 + additiveDamage / 100;
    const multiMultiplier = 1 + multiDamage / 100;
    const critMultiplier = 1 + (critChance / 100) * (critDamage / 100);
    const overpowerMultiplier = 1 + (overpowerChance / 100) * (overpowerDamage / 100);
    const vulnerableMultiplier = 1.2 * (1 + vulnerableDamage / 100);
    const shadowMultiplier = 1 + shadowDamage / 100;
    const rankMultiplier = 1 + (ranks - 1) * 0.15;
    const doubleMultiplier = 1 + doubleChance / 100;
    const waveMultiplier = tidalWaves;

    const totalDamage = Math.round(
      baseDamage *
        additiveMultiplier *
        multiMultiplier *
        critMultiplier *
        overpowerMultiplier *
        vulnerableMultiplier *
        shadowMultiplier *
        rankMultiplier *
        doubleMultiplier *
        waveMultiplier
    );

    this.displayResult(totalDamage);
    this.displayBreakdown({
      baseDamage,
      additiveMultiplier,
      multiMultiplier,
      critMultiplier,
      overpowerMultiplier,
      vulnerableMultiplier,
      shadowMultiplier,
      rankMultiplier,
      doubleMultiplier,
      waveMultiplier
    });

    return totalDamage;
  }

  displayResult(damage) {
    const resultEl = document.getElementById('damageResult');
    if (resultEl) {
      resultEl.textContent = damage.toLocaleString();
    }
  }

  displayBreakdown(multipliers) {
    const breakdownEl = document.getElementById('damageBreakdown');
    if (!breakdownEl) return;

    // Clear existing content
    while (breakdownEl.firstChild) {
      breakdownEl.removeChild(breakdownEl.firstChild);
    }

    const lines = [
      `Base: ${Math.round(multipliers.baseDamage).toLocaleString()}`,
      `Additive: x${multipliers.additiveMultiplier.toFixed(2)}`,
      `Multiplicative: x${multipliers.multiMultiplier.toFixed(2)}`,
      `Crit (Avg): x${multipliers.critMultiplier.toFixed(2)}`,
      `Overpower (Avg): x${multipliers.overpowerMultiplier.toFixed(2)}`,
      `Vulnerable: x${multipliers.vulnerableMultiplier.toFixed(2)}`,
      `Shadow: x${multipliers.shadowMultiplier.toFixed(2)}`,
      `Skill Ranks: x${multipliers.rankMultiplier.toFixed(2)}`,
      `Double: x${multipliers.doubleMultiplier.toFixed(2)}`,
      `Waves: x${multipliers.waveMultiplier}`
    ];

    lines.forEach((line, index) => {
      if (index > 0) {
        breakdownEl.appendChild(document.createElement('br'));
      }
      breakdownEl.appendChild(document.createTextNode(line));
    });
  }

  getResult() {
    const resultEl = document.getElementById('damageResult');
    return resultEl?.textContent || '0';
  }
}
