/**
 * Blood Wave Guide - Main orchestrator
 */

import { ParticleSystem } from './ParticleSystem.js';
import { ThemeManager } from './ThemeManager.js';
import { ScrollEffects } from './ScrollEffects.js';
import { SkillManager } from './SkillManager.js';
import { DamageCalculator } from './DamageCalculator.js';
import { GearComparison } from './GearComparison.js';
import { SearchHandler } from './SearchHandler.js';
import { highlightElement, showToast } from '../utils/dom.js';

export class BloodWaveGuide {
  constructor() {
    this.themeManager = new ThemeManager();
    this.particleSystem = new ParticleSystem();
    this.scrollEffects = new ScrollEffects(() => this.playSound('click'));
    this.skillManager = new SkillManager(() => this.playSound('click'));
    this.damageCalculator = new DamageCalculator();
    this.gearComparison = new GearComparison(() => this.playSound('click'));
    this.searchHandler = new SearchHandler();
  }

  init() {
    this.themeManager.init();
    this.particleSystem.init();
    this.scrollEffects.init();
    this.skillManager.init();
    this.damageCalculator.init();
    this.gearComparison.init();
    this.searchHandler.init();

    this.setupNavigation();
    this.setupShareFeature();
    this.setupImageFallback();
    this.setupBuildVariants();
    this.setupAccessibility();

    this.logWelcome();
  }

  playSound(type) {
    if (!this.themeManager.isSoundEnabled()) return;
    console.log(`Playing sound: ${type}`);
  }

  setupNavigation() {
    const navToggle = document.getElementById('navToggle');
    const navMenu = document.getElementById('navMenu');

    if (!navToggle || !navMenu) return;

    navToggle.addEventListener('click', () => {
      navMenu.classList.toggle('active');
      this.playSound('menu');
    });

    // Close nav when clicking outside
    document.addEventListener('click', e => {
      if (!navMenu.contains(e.target) && !navToggle.contains(e.target)) {
        navMenu.classList.remove('active');
      }
    });

    // Escape to close
    document.addEventListener('keydown', e => {
      if (e.key === 'Escape') {
        navMenu.classList.remove('active');
      }
      this.handleKeyboardShortcuts(e);
    });

    // Swipe to close on mobile
    let touchStartX = 0;
    navMenu.addEventListener('touchstart', e => {
      touchStartX = e.changedTouches[0].screenX;
    }, { passive: true });

    navMenu.addEventListener('touchend', e => {
      const touchEndX = e.changedTouches[0].screenX;
      if (touchStartX - touchEndX > 50) {
        navMenu.classList.remove('active');
      }
    }, { passive: true });
  }

  setupShareFeature() {
    const shareBtn = document.getElementById('shareBuild');
    if (!shareBtn) return;

    shareBtn.addEventListener('click', () => {
      const int = document.getElementById('intelligence')?.value || '0';
      const dmg = this.damageCalculator.getResult();
      const points = `${this.skillManager.getUsedPoints()}/${this.skillManager.getTotalPoints()}`;

      const summary =
        'D4 Shadow Blood Wave Necro Build\n' +
        '-----------------------------------\n' +
        `Intelligence: ${int}\n` +
        `Est. Damage: ${dmg}\n` +
        `Skill Points: ${points}\n` +
        '-----------------------------------\n' +
        'Generated by Advanced Necro Guide';

      navigator.clipboard.writeText(summary).then(() => {
        highlightElement(shareBtn);
        const originalTitle = shareBtn.title;
        shareBtn.title = 'Copied!';
        setTimeout(() => (shareBtn.title = originalTitle), 2000);

        const icon = shareBtn.querySelector('i');
        if (icon) {
          icon.className = 'fas fa-check';
          setTimeout(() => (icon.className = 'fas fa-share-alt'), 2000);
        }
      });
      this.playSound('click');
    });
  }

  setupImageFallback() {
    document.querySelectorAll('img').forEach(img => {
      img.addEventListener('error', () => {
        img.src = 'https://via.placeholder.com/64/8b0000/ffffff?text=D4';
        img.classList.add('image-fallback');
      });
    });
  }

  setupBuildVariants() {
    document.querySelectorAll('.variant-tab').forEach(tab => {
      tab.addEventListener('click', () => {
        const variant = tab.dataset.variant;

        document.querySelectorAll('.variant-tab').forEach(t => t.classList.remove('active'));
        tab.classList.add('active');

        document.querySelectorAll('.variant-content').forEach(content => {
          content.classList.remove('active');
          if (content.dataset.variant === variant) {
            content.classList.add('active');
          }
        });
      });
    });
  }

  setupAccessibility() {
    const buttons = [
      document.getElementById('navToggle'),
      document.getElementById('themeSwitcher'),
      document.getElementById('soundToggle'),
      document.getElementById('shareBuild'),
      document.getElementById('searchToggle')
    ];

    buttons.forEach(btn => {
      if (!btn) return;
      btn.addEventListener('keydown', e => {
        if (e.key === 'Enter' || e.key === ' ') {
          e.preventDefault();
          btn.click();
        }
      });
    });
  }

  handleKeyboardShortcuts(e) {
    if (e.key >= '1' && e.key <= '4') {
      const skillSlots = document.querySelectorAll('.skill-slot');
      const index = parseInt(e.key) - 1;
      if (skillSlots[index]) {
        this.skillManager.activateSkill(skillSlots[index]);
      }
    }
  }

  logWelcome() {
    console.log(`
        [Blood Wave] Advanced Shadow Blood Wave Necromancer Guide Loaded!

        Built for Season 11 - Divine Intervention
        Features: Interactive calculators, skill tracking, damage simulation

        Controls:
        - Click skills to activate
        - Use number keys 1-4 for quick skill selection
        - ESC to close menus
        - Click gear for comparisons

        May Rathma's power flow through you!
        `);
  }
}
