/**
 * Search Handler - Fuzzy search with Fuse.js
 */

import Fuse from 'fuse.js';
import { searchIndex } from '../data/searchIndex.js';

export class SearchHandler {
  constructor() {
    this.overlay = null;
    this.input = null;
    this.results = null;
    this.fuse = null;
  }

  init() {
    this.overlay = document.getElementById('searchOverlay');
    this.input = document.getElementById('searchInput');
    this.results = document.getElementById('searchResults');

    if (!this.overlay || !this.input || !this.results) return;

    this.fuse = new Fuse(searchIndex, {
      keys: ['title', 'content'],
      threshold: 0.4,
      includeMatches: true
    });

    this.setupEventListeners();
  }

  setupEventListeners() {
    const toggle = document.getElementById('searchToggle');
    if (toggle) {
      toggle.addEventListener('click', () => this.open());
    }

    this.overlay.addEventListener('click', e => {
      if (e.target === this.overlay) this.close();
    });

    this.input.addEventListener('input', e => this.search(e.target.value));

    // Keyboard shortcuts
    document.addEventListener('keydown', e => {
      if ((e.ctrlKey || e.metaKey) && e.key === 'k') {
        e.preventDefault();
        this.open();
      }
      if (e.key === 'Escape') {
        this.close();
      }
    });
  }

  open() {
    this.overlay.classList.add('active');
    this.input.focus();
    this.input.value = '';
    this.clearResults();
  }

  close() {
    this.overlay.classList.remove('active');
  }

  clearResults() {
    while (this.results.firstChild) {
      this.results.removeChild(this.results.firstChild);
    }
  }

  search(query) {
    this.clearResults();

    if (!query.trim()) return;

    const results = this.fuse.search(query);

    if (results.length === 0) {
      const noResults = document.createElement('div');
      noResults.className = 'search-no-results';
      noResults.textContent = 'No results found';
      this.results.appendChild(noResults);
      return;
    }

    results.slice(0, 8).forEach(result => {
      this.results.appendChild(this.createResultItem(result.item));
    });
  }

  createResultItem(item) {
    const div = document.createElement('div');
    div.className = 'search-result-item';
    div.dataset.section = item.section;

    const titleDiv = document.createElement('div');
    titleDiv.className = 'title';

    const icon = document.createElement('i');
    icon.className = 'fas fa-arrow-right';
    titleDiv.appendChild(icon);
    titleDiv.appendChild(document.createTextNode(' ' + item.title));

    const contextDiv = document.createElement('div');
    contextDiv.className = 'context';
    contextDiv.textContent = item.content;

    div.appendChild(titleDiv);
    div.appendChild(contextDiv);

    div.addEventListener('click', () => {
      this.close();
      const section = document.getElementById(item.section);
      if (section) {
        section.scrollIntoView({ behavior: 'smooth' });
      }
    });

    return div;
  }
}
